import React, { Component } from "react";
import { Link } from "react-router-dom";

class NavBar extends Component {
  state = {};

  render() {
    return (
      <nav className="navbar navbar-expand-lg bg-white fixed-top border-bottom">
        <a className="navbar-brand text-purple" href="/home">
          <img
            src="./logo.svg"
            width="30"
            height="30"
            className="d-inline-block align-top"
            alt=""
          ></img>
          Jaam
        </a>

        <button
          className="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarResponsive"
          aria-controls="navbarResponsive"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="fa fa-bars"></span>
        </button>

        <div className="collapse navbar-collapse" id="navbarResponsive">
          <ul className="navbar-nav mr-auto">
            <Link to="/home" className="nav-link nav-link-fade-up">
              <li className="nav-item mx-0 mx-lg-1">Product Features</li>
            </Link>
            <Link to="/pricing" className="nav-link nav-link-fade-up">
              <li className="nav-item mx-0 mx-lg-1">Pricing</li>
            </Link>
            <Link to="/contact" className="nav-link nav-link-fade-up">
              <li className="nav-item mx-0 mx-lg-1">Contact Sales</li>
            </Link>
          </ul>
          <form className="form-inline my-2 my-lg-0">
            <ul className="navbar-nav mr-auto">
              <Link to="/join" className="nav-link nav-link-fade-up">
                <li className="nav-item mx-0 mx-lg-1">Join</li>
              </Link>
              <Link to="/signin" className="nav-link nav-link-fade-up">
                <li className="nav-item mx-0 mx-lg-1">Sign in</li>
              </Link>
              <Link to="/signup" className="btn btn-purple btn-sm my-2">
                <li className="nav-item mx-0 mx-lg-1">Try for free</li>
              </Link>
            </ul>
          </form>
        </div>
      </nav>
    );
  }
}

export default NavBar;
