import React, { Component } from "react";
import { Link } from "react-router-dom";

class SignUp extends Component {
  state = {};
  render() {
    return (
      <div style={{ minHeight: "93vh" }} className="background">
        <div className="row no-gutters">&nbsp;</div>
        <div className="row no-gutters">&nbsp;</div>
        <div className="row no-gutters">&nbsp;</div>
        <div className="row no-gutters">&nbsp;</div>
        <div className="row no-gutters">&nbsp;</div>
        <div className="row no-gutters">
          <div className="col-md-4">&nbsp;</div>
          <div className="col-md-4">
            <div className="form-group mt-4">
              <h2 className="text-center">Sign up, Try for Free!</h2>
            </div>

            <div className="form-group mt-4">
              <button
                type="button"
                className="offset-md-2 col-md-8 offset-md-2 btn btn-default btn-lg text-left"
              >
                <i className="fa fa-google text-danger"></i>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Sign up with Google
              </button>
            </div>
            <div className="form-group mt-4">
              <button
                type="button"
                className="offset-md-2 col-md-8 offset-md-2 btn btn-default btn-lg text-left"
              >
                <i className="fa fa-facebook text-primary"></i>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Sign up with Facebook
              </button>
            </div>
            <div className="form-group mt-4">
              <button
                type="button"
                className="offset-md-2 col-md-8 offset-md-2 btn btn-default btn-lg text-left"
              >
                <i className="fa fa-linkedin text-info"></i>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Sign up with LinkedIn
              </button>
            </div>
            <div className="form-group mt-4">
              <div class="form-inline offset-md-2">
                <p>Already have an account?</p>
                <Link
                  to="/signin"
                  class="nav-link py-3 px-0 px-lg-3 rounded js-scroll-trigger text-muted"
                >
                  <p class="nav-item mx-0 mx-lg-1 text-purple">Sign In</p>
                </Link>
              </div>
            </div>
          </div>
          <div className="col-md-4">&nbsp;</div>
        </div>
      </div>
    );
  }
}

export default SignUp;
