import React, { Component } from "react";

class Join extends Component {
  state = {};

  render() {
    return (
      <div style={{ minHeight: "93vh" }} className="background">
        <div className="row no-gutters">&nbsp;</div>
        <div className="row no-gutters">&nbsp;</div>
        <div className="row no-gutters">&nbsp;</div>
        <div className="row no-gutters">&nbsp;</div>
        <div className="row no-gutters">&nbsp;</div>
        <div className="row no-gutters">
          <div className="col-md-3">&nbsp;</div>
          <div className="col-md-6">
            <div className="row mt-4">
              <div className="col-md-12">
                <h2 className="text-center">Join a meeting</h2>
              </div>
            </div>
            <div className="row mt-4">
              <div class="col-md-12">
                <input
                  type="text"
                  class="form-control form-control-xl"
                  id="txtMeetingCode"
                  placeholder="Enter the meeting name"
                ></input>
              </div>
            </div>
            <div className="row mt-4">
              <div class="col-md-6">
                <input
                  type="password"
                  class="form-control form-control-xl"
                  id="txtPasscode"
                  placeholder="Passcode (Optional)"
                ></input>
              </div>
              <div class="col-md-6">
                <input
                  type="text"
                  class="form-control form-control-xl"
                  id="txtName"
                  placeholder="Enter your name"
                ></input>
              </div>
            </div>
            <div className="row mt-4 mb-4">
              <button type="button" class="col-md-12 btn btn-purple btn-lg">
                Join
              </button>
            </div>
          </div>
          <div className="col-md-3"></div>
        </div>
      </div>
    );
  }
}

export default Join;
