import React, { Component } from "react";
import { Link } from "react-router-dom";

class Pricing extends Component {
  state = {};
  render() {
    return (
      <div style={{ minHeight: "93vh" }} className="background">
        <div className="row no-gutters">&nbsp;</div>
        <div className="row no-gutters">&nbsp;</div>
        <div className="row no-gutters">&nbsp;</div>
        <div className="row no-gutters">&nbsp;</div>
        <div className="row no-gutters">&nbsp;</div>
        <div className="row no-gutters">
          <div className="col-md-3"></div>
          <div className="col-md-6 text-center">
            <h2>Our pricing options</h2>
            <p>
              Rather than surprise fees, we bundle our products into simple,
              transparent subscription tiers that support your financial
              wellness.
            </p>
          </div>
          <div className="col-md-3"></div>
        </div>
        <div className="row no-gutters">&nbsp;</div>
        <div className="row no-gutters">
          <div className="col-md-3">&nbsp;</div>
          <div className="col-md-6">
            <div className="row no-gutters">
              <div className="col-md-5">
                <div className="card shadow">
                  <div className="card-body">
                    <h5 className="card-title">Basic</h5>
                    <p className="card-title">Personal Meeting</p>
                    <h5 className="card-title">Free</h5>
                    <div className="card-text">
                      <ul>
                        <li>Host up to 100 participants</li>
                        <li>Unlimited 1 to 1 meetings</li>
                        <li>40 mins limit on group meetings</li>
                      </ul>
                    </div>
                    <Link to="/signup" class="col-md-12 btn btn-purple btn-lg">
                      Sign up today
                    </Link>
                  </div>
                </div>
              </div>
              <div className="col-md-2"></div>
              <div className="col-md-5">
                <div className="card shadow">
                  <div className="card-body">
                    <h5 className="card-title">Pro</h5>
                    <p className="card-title">Great for Small Teams</p>
                    <h5 className="card-title">$14.99/mo/host</h5>
                    <div className="card-text">
                      <ul>
                        <li>All Basic features +</li>
                        <li>Meeting duration limit is 24 hrs</li>
                        <li>Admin feature controls</li>
                      </ul>
                    </div>
                    <Link to="/signup" class="col-md-12 btn btn-purple btn-lg">
                      Sign up today
                    </Link>
                  </div>
                </div>
              </div>
            </div>
          </div>

          <div className="col-md-3">&nbsp;</div>
        </div>
      </div>
    );
  }
}

export default Pricing;
