import React, { Component } from "react";
import image5 from "../assets/image5.png";
import { Link } from "react-router-dom";

class Home extends Component {
  state = {};
  render() {
    return (
      <div style={{ minHeight: "85vh" }}>
        <section>
          <div
            id="carouselExampleIndicators"
            class="carousel slide"
            data-ride="carousel"
          >
            <ol class="carousel-indicators">
              <li
                data-target="#carouselExampleIndicators"
                data-slide-to="0"
                class="active"
              ></li>
              <li
                data-target="#carouselExampleIndicators"
                data-slide-to="1"
              ></li>
              <li
                data-target="#carouselExampleIndicators"
                data-slide-to="2"
              ></li>
              <li
                data-target="#carouselExampleIndicators"
                data-slide-to="3"
              ></li>
            </ol>
            <div class="carousel-inner">
              <div class="carousel-item active image1">
                <div className="row" style={{ height: 40 + "vh" }}>
                  &nbsp;
                </div>
                <div className="row">
                  <div className="col-md-1">&nbsp;</div>
                  <div className="col-md-8 text-white">
                    <h1 className="font-weight-bolder">
                      Bored of Remote Meetings?
                    </h1>
                    <p style={{ fontSize: 40 }}>
                      Experience our fun filled, simplied and{" "}
                    </p>
                    <p style={{ fontSize: 40 }}>secured meetings</p>
                    <Link
                      to="/signup"
                      className="col-md-5 btn btn-orange btn-lg text-white"
                    >
                      Try for free
                    </Link>
                  </div>
                  <div className="col-md-3">&nbsp;</div>
                </div>
              </div>
              <div class="carousel-item image2"></div>
              <div class="carousel-item image3"></div>
              <div class="carousel-item image4"></div>
            </div>
            <a
              class="carousel-control-prev"
              href="#carouselExampleIndicators"
              role="button"
              data-slide="prev"
            >
              <span
                class="carousel-control-prev-icon"
                aria-hidden="true"
              ></span>
              <span class="sr-only">Previous</span>
            </a>
            <a
              class="carousel-control-next"
              href="#carouselExampleIndicators"
              role="button"
              data-slide="next"
            >
              <span
                class="carousel-control-next-icon"
                aria-hidden="true"
              ></span>
              <span class="sr-only">Next</span>
            </a>
          </div>
        </section>
        <section className="bg-warning">
          <div className="row" style={{ height: 5 + "vh" }}>
            &nbsp;
          </div>
          <div className="row">
            <div className="col-md-6">
              <img src={image5} width="100%" alt=""></img>
            </div>
            <div className="col-md-6 text-white">
              <h3 className="font-weight-normal mb-3">
                You see your team in cool squares!
              </h3>
              <p>Easy to switch between speaker and gallery view</p>
              <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit.</p>
              <p>Quisque gravida quam ac tempor mattis.</p>
              <p>Maecenas neque arcu, placerat</p>
            </div>
          </div>
          <div></div>
        </section>
      </div>
    );
  }
}

export default Home;
